// +++ +++ config +++ +++
$white-bcg: map-get($global-colors, white);
$light-gray-bcg: map-get($global-colors, light-gray);
$text-white: map-get($font-colors, white);
$text-orange: map-get($font-colors, orange);

// +++ +++ extends +++ +++
%station-data {
	width: 33%;
	min-height: 90px;
	padding: .250rem;
	text-align: center;
	background-color: green;
	color: $text-white;
}

// +++ +++ genreal content +++ +++
.data__container {
	width: 100%;
	padding: .750rem;
	background-color: $white-bcg;
}
.data__container-header {
	text-transform: uppercase;
	text-align: center;
}
.data__container-graybcg {
	background-color: $light-gray-bcg;
}
// +++ IJP data +++
.station__container {
	width: 100%;
	margin-top: .500rem;
	padding: .250rem;
	background-color: $light-gray-bcg;
}
.station__container-header {
	text-align: center;
	margin-bottom: .250rem;
}
.station__data-ijp {
	@extend %station-data;
}
.data__ijp, .data__pm2, .data__pm1 {
	margin: .250rem 0;
	font-size: 2rem;
	font-weight: 400;
}
.text__ijp, .units {
	font-size: .9rem;
}
.station__data-pm1 {
	@extend %station-data;
}
.station__data-pm2 {
	@extend %station-data;
}
.data__bcg-dgreen {
	background-color: #1b5e20;
}
.data__bcg-green {
	background-color: #8bc34a;
}
.data__bcg-yellow {
	background-color: #fdd835;
}
.data__bcg-orange {
	background-color: #ff5722;
}
.data__bcg-red {
	background-color: #b71c1c;
}
.data__bcg-black {
	background-color: #000;
}
// +++ Weather data +++
.data__container-weather {
	margin-top: .500rem;
	padding: 1rem 0;
	color: #fff;
	text-align: center;
}
.weather__currnet-condition {
	width: 45%;
	font-size: 4rem;
}
.weather__currnet-temp {
	width: 50%;
	font-size: 3.25rem;
	span {
		font-weight: 400;
	}
}